import React from 'react'
import { useField } from '../hooks/index'

export const Button = ({ type, text, onClick }) => (
    <button className="btn btn-primary" type={type} onClick={onClick}>
        {text}
    </button>
)

const LoginForm = ({ onSubmit }) => {
    const usernameValue = useField('username')
    const passwordValue = useField('password')

    const handleSubmit = e => {
        e.preventDefault()
        onSubmit({
            username: usernameValue.value,
            password: passwordValue.value
        })
    }

    return (
        <div className="jumbotron" style={{ alignItems:'center',marginLeft:'20%',marginRight:'50%' }}>
            <form onSubmit={handleSubmit}>
                <div className="form-group col-md-6">
                    <label htmlFor="username">Username</label>
                    <input className="form-control" {...usernameValue} reset="" />
                    <br />
                    <label htmlFor="password">Password</label>
                    <input className="form-control" {...passwordValue} reset="" />
                    <br />
                    <Button type="submit" text="Login" />
                </div>
            </form>
        </div>
    )
}
export default LoginForm
