import React from 'react'
import { Button } from '../components/LoginForm'

export const Input = ({ inputValue, handler, type, placeholder }) => (
    <input
        value={inputValue}
        onChange={handler}
        placeholder={placeholder}
        type={type}
    />
)

const AddBlogsForm = ({
    onSubmit,
    handleTitle,
    handleAuthor,
    handleUrl,
    handleLikes
}) => {
    return (
        <div>
            {
                <form onSubmit={onSubmit}>
                    <div className="form-group">
                        <Input
                            className="form-control"
                            placeholder="title"
                            value="title"
                            handler={handleTitle}
                        /> <br />

                        <Input
                            className="form-control"
                            placeholder="author"
                            value="author"
                            handler={handleAuthor}
                        /><br />

                        <Input
                            className="form-control"
                            placeholder="url"
                            value="url"
                            type="url"
                            handler={handleUrl}
                        /><br />

                        <Input
                            className="form-control"
                            placeholder="likes"
                            type="number"
                            value="likes"
                            handler={handleLikes}
                        /><br />

                        <Button style={{ marginTop:'5px' }}type="submit" text="Create" />
                    </div>
                </form>
            }
        </div>
    )
}
export default AddBlogsForm
