import React, { useState } from 'react'
import PropTypes from 'prop-types'
import '../index.css'
import blogService from '../services/blogs'

const Blog = props => {
    const [itemObj, setItemObj] = useState([])
    const [visible, setVisible] = useState(false)

    const hideWhenVisible = { display: visible ? 'none' : '' }
    const showWhenVisible = { display: visible ? '' : 'none' }

    const toggleVisibility = () => {
        setVisible(!visible)
    }

    const addLikes = async blog => {
        try {
            const blogObj = { ...blog }
            blogObj.likes++
            await blogService.updateLikes(blogObj.id, blog)
            setItemObj(blogObj)
        } catch (error) {
            alert()
            let r = window.confirm(`${error.message}! Would you like to refresh?`)
            if (r === true) {
                window.location.reload()
            } else {
                return
            }
        }
    }

    const createEachItems = async item =>
        await setItemObj({
            title: item.title,
            author: item.author,
            url: item.url,
            likes: item.likes,
            user: item.user,
            id: item.id
        })

    const itemsTitleAuthor = props.blogs.map(item => {
        const isLogged = item.user.username.indexOf(props.user) > -1
        return (
            <div
                key={item.title}
                className="blogStyle form-row"
                data-testid="visible"
            >
                <span
                    style={hideWhenVisible}
                    onClick={toggleVisibility}
                    className="ptr"
                >
                    {item.title}
                </span>
                <div
                    key={item.id}
                    onClick={() => createEachItems(item)}
                    className="ptr form-group col-md-6"
                    data-testid="createItems"
                >
                    {item.author}
                    {isLogged ? (
                        <button
                            className="btn btn-danger"
                            style={{ marginLeft:'150px',display:'inline-block' }}
                            onClick={() => props.deleteList(item, setItemObj)}
                        >
              remove
                        </button>
                    ) : (
                        <button className="hide"></button>
                    )}
                    <hr />
                </div>
            </div>
        )
    })

    const eachItems = () =>
        itemObj.title === undefined ? (
            <div className="hide" data-testid="invisible"></div>
        ) : (
            <div className="card text-center jumbotron" data-testid="revisible">
                <div className="card-header"

                    style={showWhenVisible}
                    onClick={toggleVisibility}

                    data-testid="spanvisible"
                >
                    {itemObj.title}
                </div>
                <div className="card-body">

                    <label>Follow:</label> <a href={itemObj.url}>{itemObj.url}</a>
                </div>
                {`${itemObj.likes} likes `}
                <button className="card-text btn btn-primary" onClick={() => addLikes(itemObj)}>like</button>
                <small>{` added by ${itemObj.author}`}</small>
            </div>

        )

    return (
        <div className="d-flex flex-column mb-4">
            <div className="p-2 flex-fill"  data-testid="eachitems">{eachItems()}</div>
            <div className="p-2 flex-fill" >
                {itemsTitleAuthor}
            </div>
        </div>
    )
}

Blog.propTypes = {
    blogs: PropTypes.array.isRequired,
    deleteList: PropTypes.func.isRequired,
    user: PropTypes.string.isRequired
}

export default Blog
